@media screen {
  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local(Roboto), local(Roboto-Regular), url("https://fonts.gstatic.com/s/roboto/v19/KFOmCnqEu92Fr1Me5g.woff") format("woff"), url("https://fonts.gstatic.com/s/roboto/v19/KFOmCnqEu92Fr1Me4A.woff2") format("woff2");
  }
  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    src: local("Roboto Medium"), local(Roboto-Medium), url("https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmEU9vAA.woff") format("woff"), url("https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmEU9vBg.woff2") format("woff2");
  }
  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    src: local("Roboto Bold"), local(Roboto-Bold), url("https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmWUlvAA.woff") format("woff"), url("https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmWUlvBg.woff2") format("woff2");
  }
}
